import React, { useCallback, useState } from "react"
import WebTorrent, { Torrent, TorrentFile } from "webtorrent"
import "./App.css"

var client = new WebTorrent()
//magnet:?xt=urn:btih:1c28ff99b2f7a84d57d38b48211bfedf513915f5&tr=http%3A%2F%2Facademictorrents.com%2Fannounce.php&tr=udp%3A%2F%2Ftracker.coppersurfer.tk%3A6969&tr=udp%3A%2F%2Ftracker.opentrackr.org%3A1337%2Fannounce&tr=udp%3A%2F%2Ftracker.leechers-paradise.org%3A6969

//'magnet:?xt=urn:btih:08ada5a7a6183aae1e09d831df6748d566095a10&dn=Sintel&tr=udp%3A%2F%2Fexplodie.org%3A6969&tr=udp%3A%2F%2Ftracker.coppersurfer.tk%3A6969&tr=udp%3A%2F%2Ftracker.empire-js.us%3A1337&tr=udp%3A%2F%2Ftracker.leechers-paradise.org%3A6969&tr=udp%3A%2F%2Ftracker.opentrackr.org%3A1337&tr=wss%3A%2F%2Ftracker.btorrent.xyz&tr=wss%3A%2F%2Ftracker.fastcast.nz&tr=wss%3A%2F%2Ftracker.openwebtorrent.com&ws=https%3A%2F%2Fwebtorrent.io%2Ftorrents%2F&xs=https%3A%2F%2Fwebtorrent.io%2Ftorrents%2Fsintel.torrent'

//magnet:?xt=urn:btih:52da574b6412862e199abeaea63e51bf8cea2140&tr=http%3A%2F%2Facademictorrents.com%2Fannounce.php&tr=udp%3A%2F%2Ftracker.coppersurfer.tk%3A6969&tr=udp%3A%2F%2Ftracker.opentrackr.org%3A1337%2Fannounce&tr=udp%3A%2F%2Ftracker.leechers-paradise.org%3A6969

function App() {
	const [magnetURI, setMagnetURI] = useState<string>("...")
	const handleInput = useCallback((e: React.ChangeEvent<HTMLInputElement>) => {
		const files: FileList | null = e.target.files
		if (files) {
			client.seed(files, (torrent: Torrent) => {
				console.log("Client is seeding " + torrent.magnetURI)
			})
		}
	}, [])
	const initTorrent = useCallback(() => {
		console.log(magnetURI)
		client.on("error", function (err: any) {
			console.error("ERROR: ", err.message)
		})
		client.add(magnetURI, (torrent: Torrent) => {
			console.log("got torrent metadata")
			console.log(
				"Torrent info hash: " +
					torrent.infoHash +
					" " +
					'<a href="' +
					torrent.magnetURI +
					'" target="_blank">[Magnet URI]</a> ' +
					'<a href="' +
					torrent.torrentFileBlobURL +
					'" target="_blank" download="' +
					torrent.name +
					'.torrent">[Download .torrent]</a>'
			)

			setInterval(function () {
				console.log("Progress: " + (torrent.progress * 100).toFixed(1) + "%")
			}, 5000)
			console.log("client is downloading", torrent.infoHash)
			torrent.files.forEach((file: TorrentFile) => {
				console.log(file)
				file.appendTo("body")
			})
		})
	}, [magnetURI])
	return (
		<div className='App'>
			<input id='torrent' name='file' onInput={handleInput} type='file'></input>
			<input
				type='text'
				value={magnetURI}
				onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
					setMagnetURI(e.target.value)
				}}></input>
			<button onClick={initTorrent}> start torrent </button>
		</div>
	)
}

export default App
